import dayjs from "dayjs";

import { constants } from "../../utils/constants";
import { EventsItemType } from "../../types/eventsItem";

export const getOriginData = () => {
  const originData: EventsItemType[] = [];
  for (let i = 0; i < 1; i++) {
    originData.push({
      id: i.toString(),
      name: ` ${i}`,
      startDate:
        i % 2 === 0
          ? dayjs(new Date()).format(constants.dateFormat).toString()
          : dayjs(new Date(new Date().setDate(21)))
              .format(constants.dateFormat)
              .toString(),
      endDate:
        i % 2 === 0
          ? dayjs(new Date()).format(constants.dateFormat).toString()
          : dayjs(new Date(new Date().setDate(21)))
              .format(constants.dateFormat)
              .toString(),
      type: i % 2 === 0 ? "" : "",
      orientation: i % 2 === 0 ? "" : "",
      ageRestrictions: i % 2 === 0 ? "" : "",
      venue: i % 2 === 0 ? "-" : ".  20",
      financialFeatures: " ",
    });
  }
  return originData;
};
